@import "ScssHelpers/helpers-all.scss";

.category_list {}

.category_list__info {
	max-width: 110rem;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 6.7rem;
		}
		@include bp($point_3) {
			margin-bottom: 5rem;
		}
	}
}

.category_list__row {
	display: flex;
	flex-wrap: wrap;
	align-items: stretch;

	@include bp($point_4, min) {
		margin: 0 -2.4rem -2.4rem 0;
	}
}

.category_list__column {
	@include bp($point_4, min) {
		flex-grow: 1;
		max-width: 50%;
		padding: 0 2.4rem 2.4rem 0;

		&:not(
			&.category_list_link--size_mod_2,
			&.category_list_link--size_mod_3
		) {
			width: 25%;
			min-height: 16vw;
		}
		&.category_list_link--size_mod_2 {
			width: 50%;
			min-height: 19vw;
		}
		&.category_list_link--size_mod_3 {
			width: 33.333%;
			min-height: 16vw;
		}
	}
	@include bp($point_4 - 1) {
		width: 100%;
		min-height: 23.6vw;

		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}
}